(function () {
    const $like = document.querySelector('#like');
    const $counter = document.getElementsByClassName('like-counter');
    $like.addEventListener('click', () => {
        let isLike = $like.style.color === 'red';
        $like.style.color = !isLike ? 'red' : 'black'
        $counter.innerHTML = isLike ? 1 : null;
    });
})();

(function () {
    const $date = document.querySelector('#current-date');

    setInterval(() =>  $date.innerHTML = new Date().toLocaleString(), 1000);
})();

